#web {
  background-color: #333;
  height: 100vh;
  .web_inner {
    width: 58.3vw;
    height: 100%;
    margin: 0 auto;
    position: relative;
    h1{
      position: absolute; top: 15%;
      color: #e7e7e7; font-size: 2.5rem;
      margin-bottom: 2.5rem;
    }
      //swiper 커스텀
    .swiper {
      position: absolute; left:50%; top: 50%;
      transform: translate(-50%,-50%);
      width: 1000px;
      height: 48vh;
      &-button-disabled {
          visibility: hidden;
      }
      &-wrapper {
        width: 100%;
        .swiper-slide{
          margin-right: 1rem;
        }
        .swiper-slide:last-child{
          margin-right: 0;
        }
      }
    }
    .btnPrev, .btnNext {
        position: absolute; right: -5rem; z-index: 11;
        padding: 0;
        background: none;
        border: none;
        cursor: pointer;
        width: fit-content;
        height: 1.5rem;
        display: flex; align-items: center; justify-content: center;
    }
    .btnPrev {
      bottom: 18%;
    }
    .btnNext {
      bottom: 15%;
    }
  }
}

// mobile 화면에서만 적용
@media only screen and (max-width: 767px) {
  #web{
    .web_inner{
      width: 90%;
      h1{
        font-size: 2.2rem;
      }
      .btnPrev {
        bottom: 18%; right:0;
      }
      .btnNext {
        bottom: 15%; right:0;
      }
    }
  }
}

