.introText { 
  position: relative;

  .first{ 
    position: absolute; top:50%; left: 50%; z-index: 5;
    transform: translate(-50%,-90%);
    text-align: center;
    width: fit-content; height: fit-content;
    display: flex; flex-direction: column; justify-content: center; align-self: center; transition: 2s; opacity: 1;
    p.down { 
      font-weight: 400; margin-bottom: -15px; letter-spacing: 0.07em; color: #999;
    }
    h4 {
      font-weight: 700; color: #e3e3e3;
      letter-spacing: 0.1em;
    }
    p.up {
      font-weight: 300; letter-spacing: 0.2em;
      color: #7CC6CB;
    }
    p.spam {
      font-family: 'Poppins', sans-serif;
      font-size: 1.2rem; font-weight: 200; letter-spacing: 1.5rem; color: #e3e3e387; text-align: center; margin-left: 1.8rem;
      margin-bottom: 0.8rem;
      opacity: 0.5;
    }
  }
  .second { 
    opacity: 0; transition: 2s;
    position: absolute; top:50%; left: 50%;  z-index: 5;
    transform: translate(-50%,-100%);
    h4{
      font-size: 42px; font-weight: 400; text-align: center;
    }
  }
  &:hover {
    .first{opacity: 0;}
    .second{opacity: 1;}
  }
  .scrollDown {
    position: absolute; bottom: 10rem; left: 50%; z-index: 5;
    transform: translateX(-50%);
    display: flex; justify-content: center; align-items: center; flex-direction: column;
    img {
      transform: scale(0.8);
      animation:up-down 2s infinite;
      -webkit-animation:up-down 2s infinite;
    }
    p{
      font-size: 0.3rem;
    }
  }
}

#Intro {
  background-color: #222;
  z-index: -5;
  position: relative;
  .bg_vid {
    position: absolute;
    top: 0;
    left: 0;
    min-width: 100%;
    min-height: 100%;
    overflow: hidden;
    video {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      z-index: -1;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      opacity: 0.8;
      background-color: #222;
      z-index: 1;
    }
  }
}

@keyframes up-down {
  0% {padding-bottom: 0;}
  50% {padding-bottom: 0.3rem;}
  100% {padding-bottom: 0;}
}

// mobile 화면에서만 적용
@media only screen and (max-width: 767px) {
  .introText{
    width: 100%;
    height: 100vh;
    .first {
      p.down { 
        font-size: 0.9rem; 
        
      }
      h4 {
        font-size: 4rem; 
      }
      p.up {
        font-size: 0.8rem; 
      }
    }
    .second {
      h4 {
        font-size: 39px;
      }
    }
  }

}

// tablet 화면에서만 적용
@media only screen and (min-width: 768px) and (max-width:1099px) {

}

// desktop 화면에서만 적용
@media only screen and (min-width: 1100px) {
  .introText{
    height: 100vh; 
    width: 100%;
    p.down { 
      font-size: 1rem;
    }
    h4 {
      font-size: 4.5rem;
      
    }
    p.up {
      font-size: 1rem; 
    }
  }
}