
.dot-4px {
  width: 4px; height: 4px; display: block;
  background-color: #7CC6CB;
  margin-right: 7px;
}

#stack {  
  width: 100%; height: 160vh;
  position: relative;
  overflow: hidden;

  .techStack {
    position: absolute; top:5rem; right: -15rem;
    transform: rotate(-90deg);
    font-family: 'Poppins', sans-serif; font-size: 8rem;
    opacity: 0.1;
  }
  .aboutMe {
    position: absolute; top:8rem; left: -3.9em;
    transform: rotate(90deg);
    font-size: 1.2rem;
    opacity: 0.5;
  }
  .andStudy {
    position: absolute; bottom: 25rem; left: 2rem;
    font-family: 'Poppins', sans-serif; font-size: 8rem;
    opacity: 0.1; line-height: 0.9;
    span {
      font-size: 12rem;
    }
  }

  .top-wrap, .bottom-wrap{
    width: 100%; height: 80vh;
    position: relative; background-color: beige;
    overflow: hidden;
    .stackInbox{
      height: 100%;
      position: relative;
      padding: 0 2rem;
      p { font-size: 1.3rem; }
      #devTool {
        position: absolute; top:7rem; left:3rem;
        width: fit-content; 
        .imgGroup{
          display: flex; 
          > div { 
            display: flex; flex-direction: column; justify-content: end;
          }
          img { 
            margin-bottom: 1rem;
            transform: scale(0.7);
            transform-origin:50% 100%;
          }
          p { 
            font-size: 1rem;
            text-align: center; font-weight: 600; color: #7CC6CB;
          }
          div.html { margin-left: -2rem;}
          div.css{
            margin-left: -1.5rem;
          }
          div.js {
            margin-right: 0.5rem;
            img{
              transform: scale(0.7);
              transform-origin:50% 100%;
            }
          }
        }
        .devText{
          margin-top: 20px;
          p{ display: flex; align-items: baseline; font-size: 1rem; margin-bottom: 15px;}
        }
      } // dev,desiToop end
      #desiTool{
        width: fit-content;
        position:absolute; bottom:10rem; right: 3rem;
        .imgGroup{
          display: flex; 
          > div { 
            display: flex; flex-direction: column; justify-content: end;
            margin-right: 1rem;
          }
          div:last-child{
            margin-right: 0;
          }
          img { 
            margin-bottom: 1rem;
            transform: scale(0.7);
            transform-origin:50% 100%;
          }
          p { 
            font-size: 1rem;
            text-align: center; font-weight: 600; color: #7CC6CB;
          }
        }
        .desiText{
          margin-top: 20px;
          p{ display: flex; align-items: baseline; font-size: 1rem; margin-bottom: 15px;}
        }
      }
    }
  }
  .top-wrap{background-color: #292929; overflow: hidden;
    .bg-square{
      width: 300vh; height: 40vh;
      transform: rotate(-5deg);
      position: absolute; top:13vh; left: -30vw;
      background-color: #d9d9d90e;
    }
  }
  .bottom-wrap{ background-color: #222;
    .bg-square{
      width: 300vh; height: 40vh;
      transform: rotate(5deg);
      position: absolute; bottom:18vh; left: -30vw;
      background-color: #2929296f;
    }
  }
}


// mobile 화면에서만 적용
@media only screen and (max-width: 767px) {
  #stack {  
    width: 100%;
    .techStack {
      top:6rem; right: 0rem;
      font-size: 4rem;
      line-height: 0.8;
      opacity: 0.1; 
      transform: rotate(0);
    }
    .aboutMe {
      display: none;
    }
    .andStudy {
      bottom: 25rem; left: 2rem;
      font-size: 5rem;
      opacity: 0.1; line-height: 0.9;
      span {
        font-size: 10rem;
      }
    }

    .top-wrap, .bottom-wrap{
      height: 80vh;
      .stackInbox{
        padding: 0 1rem;
        #devTool {
          transform: scale(0.9);
          top:10rem; left:0rem;
          .devText{
            p {
              font-size: 0.95rem;
            }
          }
        }
        #desiTool{
          transform: scale(0.9);
          bottom:5rem; right: 0rem;
          .desiText{
            width: 440px;
            p {
              font-size: 0.95rem;
            }
          }
        }
      }
    }
    .top-wrap{
      .bg-square{
        top:15vh; left: -30vw;
      }
    }
    .bottom-wrap{
      .imgGroup{
        justify-content: flex-start;
      }
    }
  }
}

// tablet 화면에서만 적용
@media only screen and (min-width: 768px) and (max-width:1099px) {

}

// desktop 화면에서만 적용
@media only screen and (min-width: 1100px) {
  #desitool{
    .imgGroup{
      justify-content: end;
    }
  }
}