

#Footer {
  background-color: #333;
  padding: 10rem 0;
  display: block;
  .footerTop {

    display: flex; 
    align-items: center;
    justify-content: flex-start;
  
    .footerLText {
      text-align: right;
      h1{
          font-size: 3.8rem;
          line-height: 4.8rem;
        }
        h2 {
          margin-top: 1rem;
          font-size: 1.45rem;
          span{
            color:#7CC6CB;
          }
        }
      }
      .footerRText {
        font-size: 1.1rem;
        height: 18vh;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        p > span {
          font-weight: 700;
        }
      }
    }
  .inner{
    height: 30vh;
    .footerBottom{
      height: 30vh;
      width: 100%;
      margin-top: 5rem;
      .cul_line {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: flex-start;
        flex-wrap: wrap;
        height: 15vh;
        transform:scale(0.8);
      }
    } 
  }
}


// mobile 화면에서만 적용
@media only screen and (max-width: 767px) {
  .vertical-line {
    display: block; width: 60%; height: 1px;
    border-bottom: 2px solid #888;
    margin : 3rem 0 3rem 0;
    transition: 1s;
  }
  #Footer {
    height: 100%;
    .footerTop{
      flex-direction: column;
      align-items: flex-end;
      margin-right: 2rem;
      .footerLText{
        h1{
          font-size: 3.7rem;
          line-height: 4.2rem;
        }
        h2 {
          margin: 0.5rem 0;
          font-size: 1.6rem;
        }
      }
      .footerRText{
        text-align: right;
        font-size: 0.85rem;
      }
    }
    .inner {
      height: 100%;
      .footerBottom{
        height: 100%;
        width: 100%;
        margin-top: 5rem;
        .cul_line {
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          align-items: flex-start;
          flex-wrap: wrap;
          height: 50vh;
          transform:scale(0.9);
        }
      } 
    }
  }
}

// tablet 화면에서만 적용
@media only screen and (min-width: 768px) and (max-width:1099px) {

}

// desktop 화면에서만 적용
@media only screen and (min-width: 1100px) {
  .vertical-line {
    display: block; height: 90%; width: 1px;
    border-right: 3px solid #888;
    margin : 0 2.5rem;
    transition: 1s;
  }
  #FooterP{
    .footerTop{
      height: 30vh;
      margin-left: 2rem;
    }
  }

}