// mobile 화면에서만 적용
@media only screen and (max-width: 767px) {
  header {
    font-family: 'Poppins', sans-serif;
    width: 100%;
    position: fixed;
    z-index: 10;
    background-color: transparent;
    transition: background-color .3s ease-in;
  
    &:hover {
      background-color: #222;
    }    
    .head_inner {
      width: 440px;
      height: 40px;
      display: flex; 
      justify-content: center; 
      margin: 0 auto;
      h1 {
        line-height: 40px; font-size: 1.2rem;
        font-weight: 500;
        cursor: pointer;
        color: #888;
  
        display: none;
      }
      ul.gnb{
          display: flex;
        li {
          margin: 0 0.5rem;
          line-height: 40px;
          font-size: 0.8rem;
          color: #666;
          cursor: pointer;
        }
        // &:last-child{
        //   margin-right: 0;
        // }
        span { 
          line-height: 40px; font-size: 1rem; padding-bottom: 5px;
          color: #d3d3d32f;
        }
      }
    }  
  }

  .fullPage {
    width: 100%;
    display: flex; align-items: center;
    overflow: hidden;
  }
  
  .inner {
    width: 440px; height: 100%;
    margin: 0 auto;
  }  
}

// tablet 화면에서만 적용
@media only screen and (min-width: 768px) and (max-width:1099px) {

}

// desktop 화면에서만 적용
@media only screen and (min-width: 1100px) {
  header {
    font-family: 'Poppins', sans-serif;
    width: 100%;
    position: fixed;
    z-index: 10;
    background-color: transparent;
    transition: background-color .3s ease-in;
  
    &:hover {
      background-color: #222;
    }    
    .head_inner {
      width: 1100px;
      height: 40px;
      display: flex; 
      justify-content: space-between;
      margin: 0 auto;
  
      h1 {
        margin-left: 1rem;
        line-height: 40px; font-size: 1.2rem;
        font-weight: 500;
        cursor: pointer;
        color: #888;
      }
      ul.gnb{
          display: flex;
        li {
          margin: 0 1rem;
          line-height: 40px;
          font-size: 0.8rem;
          color: #666;
          cursor: pointer;
        }
        // &:last-child{
        //   margin-right: 0;
        // }
        span { 
          line-height: 40px; font-size: 1rem; padding-bottom: 5px;
          color: #d3d3d32f;
        }
      }
  
    }
  
  }
  // 1100px 이상일 때 common settings
  
  .fullPage {
    width: 100%; height: 100vh;
    display: flex; align-items: center;
    overflow: hidden;
  }
  
  .inner {
    width: 1100px; height: 100%;
    margin: 0 auto;
  }
}