#banner {
  background-color: #222;
  height: 100vh;
  .banner_inner {
    width: 58.3vw;
    height: 100%;
    margin: 0 auto;
    position: relative;
    h1{
      color: #e7e7e7; font-size: 2.5rem;
      margin-bottom: 2.5rem;
      position: absolute; top:10%; left: 0; z-index: 5;
    }
      //swiper 커스텀
    .swiper {
      width: 1000px;
      height: 68vh;
      position: absolute; top:50%; left: 50%;
      transform: translate(-50%,-50%);
      &-button-disabled {
          visibility: hidden;
      }
      &-wrapper {
        width: 100%;
        .swiper-slide{
          display: flex; 
          // align-items: end; 
          justify-content: center;
          padding-bottom: 5rem;
          transform: scale(0.6);
        }
        .swiper-slide:nth-of-type(4){
          transform: scale(0.6);
        }
        .swiper-slide:nth-of-type(5){
          transform: scale(0.6);
        }
        .swiper-slide:nth-of-type(6){
          transform: scale(0.6);
        }
        .swiper-slide:nth-of-type(7){
          transform: scale(0.15);
        }
        .swiper-slide:nth-of-type(8){
          transform: scale(0.4);
        }
      }
      &-pagination {  
        .swiper-pagination-bullet {
          width: 0.6rem; height: 0.6rem;
          margin: 0 0.5rem;
        }
      }
      .slideName {
        position: absolute; bottom: 2.5rem; left: 50%; 
        transform: translateX(-50%);
        z-index: 5;
        color: #e7e7e7;
        font-size: 1rem;
        font-weight: 300;
      }
    }
    .btnPrev, .btnNext {
      position: absolute; right: -5rem; z-index: 11;
      padding: 0;
      background: none;
      border: none;
      cursor: pointer;
      width: fit-content;
      height: 1.5rem;
      display: flex; align-items: center; justify-content: center;
      
    }
    .btnPrev {
      bottom: 18%;
    }
    .btnNext {
      bottom: 15%;
    }
  }
}


// mobile 화면에서만 적용
@media only screen and (max-width: 767px) {
  #banner{
    .banner_inner{
      width: 90%;
      h1{
        font-size: 2.2rem;
      }
      .swiper {
        &-wrapper {
          .swiper-slide:nth-of-type(4){
            transform: scale(0.6);
          }
          .swiper-slide:nth-of-type(5){
            transform: scale(0.35);
          }
          .swiper-slide:nth-of-type(6){
            transform: scale(0.6);
          }
          .swiper-slide:nth-of-type(7){
            transform: scale(0.068);
          }
          .swiper-slide:nth-of-type(8){
            transform: scale(0.17);
          }
        }
        &-pagination {  
          .swiper-pagination-bullet {
            transform: scale(0.8);
          }
        }
      }
      .btnPrev, .btnNext {
        display: none;
      }
    }
  }
}

