#aboutInbox {
  display: flex; justify-content: center; align-items: center; position: relative;
  .beSteady {
    position: absolute; top:2rem; right: -15rem; z-index: 5;
    font-family: 'Poppins', sans-serif; font-size: 8rem;
    line-height: 0.8; opacity: 0.1;
  }
  .imgBox {
    height: 450px; width: 340px;
    background-color: #777;
    margin-right: 3rem; overflow: hidden;
    box-shadow: 0 0 5px #222;
    img {
      width: 340px; height: 450px;
    }
  }
  .textBox {
    height: 100%;
    display: flex; flex-direction: column; justify-content: center;
    h1 {
      margin-bottom: 1.7rem;
      color: #51AC9D;
      span {
        font-weight: 400;
        color: #fff;
      }
    }
    p {
      font-size: 0.9rem; 
      margin-bottom: 1.3rem;
      letter-spacing: 0.5px;
      span{
        color: #51AC9D;
      }
    }
    p:last-child {
      margin-bottom: 0;
    }
  }
}

#about {
  background-color: #333;
}

// mobile 화면에서만 적용
@media only screen and (max-width: 767px) {
  #aboutInbox {
    flex-direction: column;
    height: fit-content; 
    padding : 7rem 1rem;
    .imgBox{
      margin: 0 3rem 5rem 0;
    }
  }
}

